/* main.scss - Main Stylesheet for Modern Web Project */
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
/* _reset.scss - Modern Reset for Consistent Styling */
/* ====================================
   Box Sizing
   ==================================== */
/* Apply border-box to all elements for easier sizing */
*, *::before, *::after {
  box-sizing: border-box;
}

/* ====================================
   Margin and Padding
   ==================================== */
/* Remove default margin and padding from all elements */
* {
  margin: 0;
  padding: 0;
}

/* ====================================
   Typography
   ==================================== */
/* Set default font settings on the body */
body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px; /* 1rem */
  line-height: 1.7;
  text-rendering: optimizeSpeed;
  /* Set tab size for consistent spacing */
  tab-size: 4;
}

/* Prevent line-height inheritance issues for form elements */
button, input, select, textarea {
  line-height: normal;
}

/* ====================================
   Links
   ==================================== */
/* Remove default styles from anchors */
a {
  text-decoration: none;
  color: inherit;
}

/* ====================================
   Media Elements
   ==================================== */
/* Ensure images and pictures are responsive */
img, picture {
  max-width: 100%;
  display: block;
}

/* Ensure videos and audios are responsive */
video, audio {
  display: block;
  max-width: 100%;
}

/* ====================================
   Forms
   ==================================== */
/* Inherit font styles for form elements */
input, button, textarea, select {
  font: inherit;
  border: 1px solid #ccc;
  outline: none;
}

/* Style buttons */
button {
  cursor: pointer;
  background: none;
  border: none; /* Overrides the border set above */
}

/* ====================================
   Lists
   ==================================== */
/* Remove list styles */
ul, ol {
  list-style: none;
}

/* Prevent text overflow */
h1, h2, h3, h4, h5, h6,
p, ul, ol, li, figure, blockquote, dl, dd {
  overflow-wrap: break-word;
}

/* ====================================
   Tables
   ==================================== */
/* Reset table styles */
table {
  border-collapse: collapse;
  width: 100%;
}

/* ====================================
   Accessibility
   ==================================== */
/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: rgba(119, 119, 119, 0.4666666667);
  padding: 3rem;
  font-size: 1.6rem;
}

